* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.wrapper {
  max-width: 1130px;
  background-color: aqua;
  margin: 0 auto;
  .header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 87px;
    .name {
      /* czcionka z filmiku? */
      font-family: "Lato", sans-serif;
      font-weight: 400;
      font-size: 30px;
      line-height: 30px;
      font-style: normal;
      color: #000000;
      height: 77px;
      width: 51.3%;
      margin-left: 1.85%;
      margin-top: 11px;
    }
    .form-text {
      width: 20.9%;
      min-width: 136px;
      background-color: #40483a;
      margin-right: 2.3%;
      margin-top: 11px;
      height: 90px;
      border-radius: 20px;
      box-shadow: 10px 4px 4px rgba(0, 0, 0, 0.75);
      .text {
        font-family: "Lato", sans-serif;
        font-weight: 400;
        font-style: italic;
        font-size: 30px;
        line-height: 30px;
        margin-top: 10.5px;
        color: #ffffff;
        width: 80%;
        margin-left: 18.3%;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      }
    }
  }
  .picture {
    margin-left: 13.4%;
    width: 70.8%;
    aspect-ratio: 108/100;
    background: rgba(54, 44, 44, 0.01);
    background-color: #575555;
    backdrop-filter: blur(280px);
    /* background-color: #D9D9D9; */
    // border-radius: 65% 7.8% 40% 13%;
    // border-radius: 500px 60px 300px 100px;
    // 800 739
    // border-radius: 62.5% / 67.7% 7.5% / 8.1% 37.5% / 40.6% 12.5% / 13.5%;
    border-radius: 62.5% 7.5% 37.5% 12.5%/ 67.7% 8.1% 40.6% 13.5%;
    .inner-picture {
      display: inline-block;
      /* height: 383px; */
      /* width: 534px; */
      width: 66.75%;
      aspect-ratio: 139/100;
      background-color: #ffffff;
      margin-left: 18%;
      margin-top: 140px;
      /* border-radius: 200px 20px 100px 50px; */
      // border-radius: 37.5% 4.4% 18.75% 9.375%;
      border-radius: 37.5% 3.7% 18.7% 9.7%/ 52% 5.2% 26.1% 13.1%;

      /* border-radius: 43.6% 20px 100px 50px; */
      /* 534/384 */
      /* 75 */
      /* 459 */
    }
    .h1name {
      font-family: "Inter", sans-serif;
      font-style: normal;
      font-weight: 800;
      font-size: 50px;
      line-height: 61px;
      margin-top: 26.39px;
      height: 76.26px;
      width: 41.7%;
      margin-left: 28.3%;
      text-align: center;
    }
    .look {
      font-family: "Inter", sans-serif;
      position: relative;
      top: -15.01px;
      margin-left: 28.3%;
      height: 59.5px;
      width: 46%;
      background-color: #575555;
      .inner-look {
        // position: relative;
        // top: -20px;
        display: block;
        font-family: "Inter";
        font-style: italic;
        color: black;
        font-weight: 100;
        font-size: 20px;
        line-height: 24px;
      }
    }
  }
  // img{
  //   width: 33.9%;
  //   height: auto;
  // }
  .next-profiles {
    font-family: "Lato", sans-serif;
    font-weight: 400;
    font-style: italic;
    font-size: 30px;
    line-height: 30px;
    color: #ffffff;
    margin-top: 29.67px;
    margin-left: 38.3%;
    background: #35660e;
    height: 54.88px;
    width: 19.8%;
    min-width: 160px;
    box-shadow: 10px 4px 4px rgba(0, 0, 0, 0.75);
    border-radius: 20px;
    border: none;
    .inner-next-profiles {
      position: relative;
      top: -5px;
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
  }
  .form {
    .h1form {
      text-transform: uppercase;
      width: 79.4%;
      height: 52.64px;
      background-color: #35660e;
      margin-left: 5.2%;
      justify-content: flex-end;
      margin-top: 73px;
      font-family: "Inter";
      font-style: normal;
      font-weight: 800;
      font-size: 20px;
      line-height: 33px;
      letter-spacing: 0.15em;
      display: flex;
      align-items: center;
    }

    .under-h1 {
      background: #071594;
      height: 9.29px;
      width: 14.7%;
      margin-left: 69.9%;
      margin-top: 12.39px;
    }
    .inner-form {
      margin-top: 55.74px;
      width: 66.6%;
      margin-left: 18%;
      .field {
        width: 45.9%;
        height: 43.35px;
        font-family: "Inter";
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 33px;
        display: flex;
        align-items: center;
        white-space: nowrap;
        .input {
          border: none;
        }
      }
      .checkbox {
        .input[type="checkbox"] {
          transform: scale(2.3);
          margin-right: 8.9%;
        }
        // input {
        //   width: 30px;
        //   height: 30px;

        // }
      }
      hr {
        height: 1.55px;
      }
      hr:nth-of-type(1) {
        margin-top: 46.45px;
        margin-bottom: 23.22px;
      }
      hr:nth-of-type(2) {
        margin-top: 48px;
        margin-bottom: 21.68px;
      }
      hr:nth-of-type(3) {
        margin-top: 49.55px;
        margin-bottom: 20.13px;
      }
      hr:nth-of-type(4) {
        margin-top: 50.86px;
        margin-bottom: 33.45px;
      }
      .submit {
        color: #ffffff;
        border: 1px solid #d9d9d9;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border: none;
        margin-top: 28.65px;
        margin-left: 35.2%;
        margin-bottom: 50px;
        height: 54.88px;
        background: #071594;
        box-shadow: 10px 4px 4px rgba(0, 0, 0, 0.75);
        border-radius: 20px;
        width: 29.8%;
        min-width: 102px;
        .inner-submit {
          line-height: 30px;
          font-family: "Lato";
          font-style: italic;
          font-weight: 400;
          font-size: 30px;
          position: relative;
          top: -5px;
        }
      }
    }
  }
}

@media all and (max-width: 992px) {
  .wrapper {
    .header {
      .form-text {
        .text {
          font-size: 20px;
        }
      }
    }
  }
}

@media all and (max-width: 992px) {
  .wrapper {
    .header {
      flex-direction: column;
      align-items: center;
      .name {
        text-align: center;
      }
    }
  }
}

@media all and (max-width: 576px) {
  .wrapper {
    .picture {
      .h1name {
        font-size: 30px;
      }
    }
    .next-profiles {
      display: block;
      margin: auto;
      margin-top: 29.67px;
    }
    .form {
      .h1form {
        font-size: 16px;
      }
      .inner-form {
        .field:nth-of-type(4) {
          margin: auto;
          flex-direction: column;
          // justify-content: center;
        }
        .submit {
          display: block;
          margin-left: auto;
          margin-right: auto;
        }
      }
    }
  }
}

@media all and (max-width: 345px) {
  .wrapper {
    .form {
      .inner-form {
        .field:nth-of-type(5) {
          margin: auto;
          flex-direction: column;
          // justify-content: center;
          .input {
            margin-bottom: 10px;
          }
        }
      }
    }
  }
}



